<script>
import { getRoomDetail } from '@/utils/api'
Date.prototype.addDays = function(days) {
  var date = new Date(this.valueOf())
  date.setDate(date.getDate() + days)
  return date
}
export default {
  filters: {
    priceFilter(price) {
      return `NT.${price.toLocaleString()}`
    }
  },
  data () {
    return {
      pickerDate: new Date().toISOString().substr(0, 10),
      rating: 4,
      picker: '',
      nowDate: new Date().toISOString().substr(0, 10),
      nowDateAfter90days: new Date().addDays(90).toISOString().substr(0, 10),
      amenities: [
        'Wi-Fi',
        'Breakfast',
        'Mini-Bar',
        'Room-Service',
        'Television',
        'Air-Conditioner',
        'Refrigerator',
        'Sofa',
        'Great-View',
        'Smoke-Free',
        'Child-Friendly',
        'Pet-Friendly',
      ],
      loading: false,
      post: null,
      error: null,
      room: null,
      booking: null,
      alreadyBookingDate: [
        '2019-08-28',
        '2019-08-29',
        '2019-08-30',
        '2019-10-30'
      ]
    }
  },
  mounted() {
    this.fetchRoomData()
  },
  methods: {
    addCommas(nStr) {
      nStr += ''
      const x = nStr.split('.')
      let x1 = x[0]
      const x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) {
          x1 = x1.replace(rgx, '$1' + ',' + '$2')
      }
      return x1 + x2
    },
    fetchRoomData() {
      getRoomDetail(this.getRoomIdByIndex(this.$route.params.id)).then((response) => {
        // console.log(response)
        this.room = response.room[0]
        this.booking = response.booking
        this.booking.forEach(booking => {
          this.alreadyBookingDate.push(booking.date)
        })
      })
    },
    getRoomIdByIndex(index) {
      const rooms = this.$store.getters.rooms
      const room =  rooms.find((room) => {
        return room.index === index
      })
      return room.id
    },
    allowedDates(val) {
      // console.log(this.nowDate)
      return val >= this.nowDate && val <= this.nowDateAfter90days && !this.alreadyBookingDate.includes(val)
    },
    headerDate(date) {
      if (!date) return null
      const [year, month] = date.split('-')
      return `${year}/${+month}`
    },
    reserveClick() {
      alert('還沒做！Σ( ° △ °)')
    }
  }
}
</script>

